                <div class="flex items-center space-x-3 rtl:space-x-reverse md:pl-4">
                    <span class="self-center text-2xl font-semibold whitespace-nowrap text-indigo-400">Jessa</span>
                </div>
                <button data-collapse-toggle="navbar-default" @click="navStore.showNavar"
                    type="button" class="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-500 hover:text-white focus:outline-none focus:ring-2">
                    <span class="sr-only">Open main menu</span>
