                :class="{ visible: navStore.navarState }">
                    <ul class="flex flex-col p-3 md:p-0 md:mt-0 font-medium rounded-lg md:space-x-10 rtl:space-x-reverse 
                    md:flex-row md:transparent md:h-full text-gray-400 bg-color h-[180px]">
                        <li>
                            <router-link to="/" class="block py-2 px-3 text-indigo-400 rounded md:bg-transparent md:p-0" aria-current="page">
                                 Home
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
        <div class="flex-grow mb-4 w-full max-h-full flex flex-col items-center justify-between mx-auto md:px-4 pt-2 mt-2">
            <div class="w-full h-full bg-stone-50 mt-4 px-4" id="portfolio">
                <div class="pt-20 pb-40">
                    <div class="mx-auto grid grid-cols-1 justify-items-center">
                        <div v-for="(quote, index) in quotes" :key="index"
                            class="h-80 w-[400px] rounded-lg bg-indigo-400 text-center flex flex-col justify-center items-center mb-4">
                            <p class="text-white text-[20px] pb-2">{{quote.content}}</p>
                            <span class="text-white"> - {{quote.author}}</span>
                        </div>
                    </div>
                    <div class="mx-auto grid grid-cols-1 justify-items-center">
                        <button @click="reloadPage" class="bg-indigo-400 w-80 text-white h-10 rounded-full">Refresh</button>
                    </div>
                </div>
            </div>
        </div>
        <footer class="bg-white dark:bg-gray-900 md:mx-4 mt-auto">
            <div class="w-full mx-auto p-4 md:py-8">
                <div class="sm:flex sm:items-center sm:justify-between">
                    <div href="#" class="flex items-center mb-4 sm:mb-0 space-x-3 rtl:space-x-reverse">
