<template>
    <div class="min-h-screen w-screen flex flex-col bg-indigo-300">
        <nav class="md:mx-4">
            <div class="max-w-full flex flex-wrap items-center justify-between mx-auto md:p-4 md:pr-0 md:pl-0 pr-4 pl-2 md:h-full h-20 bg-white">
                <div class="flex items-center space-x-3 rtl:space-x-reverse md:pl-4">
                    <span class="self-center text-2xl font-semibold whitespace-nowrap text-indigo-400">Jessa</span>
                </div>
                <button data-collapse-toggle="navbar-default" @click="navStore.showNavar"
                    type="button" class="inline-flex items-center p-2 w-10 h-10 justify-center text-sm text-gray-500 rounded-lg md:hidden hover:bg-gray-500 hover:text-white focus:outline-none focus:ring-2">
                    <span class="sr-only">Open main menu</span>
                    <svg class="w-5 h-5 text-grat-400" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 17 14">
                        <path stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M1 1h15M1 7h15M1 13h15"/>
                    </svg>
                </button>
                <div class="items-center justify-between hidden w-full md:flex md:w-auto md:order-1 h-full z-50" id="navbar-sticky"
                :class="{ visible: navStore.navarState }">
                    <ul class="flex flex-col p-3 md:p-0 md:mt-0 font-medium rounded-lg md:space-x-10 rtl:space-x-reverse 
                    md:flex-row md:transparent md:h-full text-gray-400 bg-color h-[180px]">
                        <li>
                            <router-link to="/" class="block py-2 px-3 text-indigo-400 rounded md:bg-transparent md:p-0" aria-current="page">
                                 Home
                            </router-link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
        <div class="mb-4 w-full max-h-full flex flex-col items-center justify-between mx-auto md:px-4 pt-2 mt-2">
            <div class="flex-col items-center w-full h-full 
            rounded-tr-[6000px] md:rounded-bl-[4000px] bg-stone-50">
                <div class="pt-20 md:pb-40 pb-20">
                    <div div class="max:h-full w-96 flex items-center justify-center bg-teal-lightest bg-indigo-300 m-auto">
                        <div class="transparent rounded shadow p-6  w-full lg:max-w-full h-full">
                            <div class="mb-4">
                                <h1 class="text-white font-bold text-[20px] text-center">Todo List</h1>
                                <div class="flex mt-4">
                                    <input v-model="task" class="appearance-none  rounded w-full py-2 px-3 mr-4 text-center" placeholder="Add Todo">
                                    <button @click="addTask" class="flex-no-shrink p-2 border-2 rounded text-gray-400 border-teal hover:text-indigo-700 bg-white">Add</button>
                                </div>
                                <div class="flex flex-col items-center my-4">
                                    <input type="text" v-model="searchQuery" placeholder="Search" id="search" class="ml-2 rounded-2xl text-center"> 
                                </div>
                            </div>
                            <div class="flex flex-col mb-4">
                                <div class="flex mb-2 items-center header w-full h-10 bg-white text-gray-400 text-center">
                                    <p class="flex-1 py-6">Details</p>
                                    <p class="flex-1 py-6">Action</p>
                                </div>
                                <div class="flex items-center header h-full w-full bg-white text-gray-400 text-center" v-for="todo in filteredTodos" :key="todo.id">
                                    <div class="datas flex w-full h-full py-2">
                                        <p class="flex-1" :class="{ check: todo.status }">{{ capitalizeFirstLetter(todo.details) }}</p>
                                        <p class="flex-1">
                                            <a href="#" class="mx-2" @click="updateData(todo.id, todo.status)">
                                                <i class="bi bi-pencil-square"></i>
                                            </a>
                                            <a href="#" @click="removeTask(todo.id)">
                                                <i class="bi bi-trash3"></i>
                                            </a>
                                        </p>
                                    </div>
                                </div>
                            </div>
                            <div class="flex mt-4 text-center bg-white">
                                <button class="items-center header w-full h-10 text-gray-400 text-center hover:text-white hover:bg-indigo-400" 
                                    v-if="hasTasks" @click="clearAllTasks">
                                    Clear Task
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <footer class="bg-white dark:bg-gray-900 md:mx-4 mt-auto">
            <div class="w-full mx-auto p-4 md:py-8">
                <div class="sm:flex sm:items-center sm:justify-between">
                    <div href="#" class="flex items-center mb-4 sm:mb-0 space-x-3 rtl:space-x-reverse">
                        <span class="self-center text-2xl font-semibold whitespace-nowrap text-indigo-400">Jessa</span>
                    </div>
                    <ul class="flex flex-wrap text-center items-center mb-6 text-sm font-medium text-gray-500 sm:mb-0 dark:text-gray-400">
                        <li>
                            <a href="#" class="hover:underline me-4 md:me-6">Home</a>
                        </li>
                        <li>
                            <a href="#" class="hover:underline me-4 md:me-6">About</a>
                        </li>
                        <li>
                            <a href="#" class="hover:underline me-4 md:me-6">Contact</a>
                        </li>
                        <li>
                            <a href="#" class="hover:underline">Policy</a>
                        </li>
                    </ul>
                </div>
                <hr class="my-6 border-gray-200 sm:mx-auto dark:border-gray-700 lg:my-8" />
                <span class="block text-sm text-gray-500 sm:text-center dark:text-gray-400">© 2023 <a href="#">Jessa Pangatungan</a>. All Rights Reserved.</span>
            </div>
        </footer>
    </div>
</template>
<script setup>
import { useNavarStore } from '../stores/navStore';
import { db } from '../firebase';
import { ref, onMounted, computed } from 'vue';
import { collection, addDoc, serverTimestamp, onSnapshot, orderBy, query, doc, deleteDoc, updateDoc, getDocs } from 'firebase/firestore';

const navStore = useNavarStore();
const task = ref('');
const todos = ref([]);
const searchQuery = ref('');
const status = ref(false);

const capitalizeFirstLetter = (str) => {
    return str.charAt(0).toUpperCase() + str.slice(1);
};

const addTask = async (e) => {
      e.preventDefault();
      try {
        await addDoc(collection(db, 'todo'), {
          details: task.value,
          status: status.value,
          date_created: serverTimestamp(),
        });
        task.value = '';
      } catch (e) {
        console.error('Error adding document: ', e);
      }
};

const getData = () => {
      const fsquery = query(collection(db, "todo"), orderBy('date_created','asc'));
      onSnapshot(fsquery,(querySnapshot) => {
        const newTasks = [];
        querySnapshot.forEach((doc) => {
          const datas = {
              id:doc.id,
              details:doc.data().details,
              status:doc.data().status,
            }
            newTasks.unshift(datas);
        });
        todos.value=newTasks;
      })
};

const updateData = (id, currentStatus) => {
      const fsquery = doc(db, 'todo', id);
      const newData = {
        status: !currentStatus,
      }
      updateDoc(fsquery, newData)
      .then(() => {
        console.log('Document successfully updated.');
      })
      .catch((error) => {
        console.error('Error updating document:', error);
      });
};

const removeTask = async (id) => {
      try{
        await deleteDoc(doc(db,'todo',id))
      }catch(e){
        console.log(e);
      }
};

const hasTasks = computed(()=> {
      return todos.value.length > 0;
});

const clearAllTasks = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, 'todo'));
        querySnapshot.forEach(async (doc) => {
          await deleteDoc(doc.ref);
        });
        todos.value = [];
      } catch (e) {
        console.log(e);
      }
};
const filteredTodos = computed(()=> {
      return todos.value.filter(todo => {
        return todo.details.toLowerCase().includes(searchQuery.value.toLowerCase());
      });
});

onMounted(()=> {
      getData()
})
</script>
<style scoped>
.visible {
  display: block !important;
}
a {
    text-decoration: none;
}
a:hover{
    background-color: rgb(135, 145, 235);
    color: white;
}
.check {
    text-decoration: line-through;
    color: rgb(109, 2, 109);
  }
</style>